import './HomeTransportista.css'
import './OportunidadesTransportista.css'

import { Capacitor } from '@capacitor/core'
import {
  BackgroundGeolocation,
  BackgroundGeolocationAuthorizationStatus,
  BackgroundGeolocationResponse,
} from '@ionic-native/background-geolocation'
import { StatusBar } from '@ionic-native/status-bar'
import { IonAlert, IonModal, IonSpinner } from '@ionic/react'
import { LatLngLiteral } from 'leaflet'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { IoChatbubbleEllipsesOutline } from 'react-icons/io5'
import { MapContainer, Marker, Polyline, TileLayer, useMap } from 'react-leaflet'
import { CameraPreview } from '@awesome-cordova-plugins/camera-preview/ngx';
import { RouteComponentProps, useLocation } from 'react-router'

import { CalificacionTransportista, HomeTransportistaFooter, NumpadModal, PageApp } from '../../components'
import ChatModal from '../../components/Chat/ChatModal'
import { ModalInfo } from '../../components/ModalInfo'
import BarraEstadoTransportista from '../../components/Transportista/BarraEstadoTransportista'
import { CompletarPerfil } from '../../components/Transportista/CompletarPerfil'
import { IniciarOportunidadIcono } from '../../components/Transportista/IconosTransportista'
import { RUTAS, RUTAS_APP, RUTAS_WEB_TRANSPORTISTA, TIPOS_INCIDENTE_INDEXES } from '../../constants'
import { leaflet_location2string, string2leaflet_location } from '../../helpers'
import {
  cambiarModo,
  centroUbicaciones,
  ICONOS_ITINERARIO,
  itinerario_marker,
  itinerario_popup,
  Loc2LatLng,
  useApiToaster,
  useConfirm,
  usePrevious,
  useToaster,
} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { GenericResponse, HojaRutaApi, ItinerarioApi, RespuestaWithInfo, Vehiculo } from '../../services/api'

const jpeg = require('jpeg-js');
const jsQR = require("jsqr");

interface MapControlerProps {
  center: LatLngLiteral
}
const MapController: React.FC<MapControlerProps> = (props) => {
  const map = useMap()

  if (map) {
    map.flyTo(props.center)
  }

  return null
}

export const HomeTransportista: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore, transportistaStore, chatStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [Center, setCenter] = useState<LatLngLiteral>({ lng: -57.63591, lat: -25.30066 })
  const [Ubi, setUbi] = useState<LatLngLiteral | null>(transportistaStore.lastLoc)
  const prevUbi = usePrevious<LatLngLiteral | null>(Ubi)
  const [Polylines, setPolylines] = useState<LatLngLiteral[] | null>()
  const [LocationOportunidad, setLocationOportunidad] = useState<LatLngLiteral | null>(null)

  const [ItinerarioActivo, setItinerarioActivo] = useState<ItinerarioApi | null>()
  const [HojaRutaActiva, setHojaRutaActiva] = useState<HojaRutaApi | null>()
  const prevItinerario = usePrevious<ItinerarioApi | null>(ItinerarioActivo)

  const [FooterReady, setFooterReady] = useState(false)
  const [GotGPSPermission, setGotGPSPermission] = useState(false)
  const [Ready, setReady] = useState(false)
  const [QRModalVisible, setQRModalVisible] = useState(false)
  const [CalificacionModalVisible, setCalificacionModalVisible] = useState(false)
  const [ModalIncidente, setModalIncidente] = useState(false)
  const [ModalConfirmarAbandono, setModalConfirmarAbandono] = useState(false)
  const [ModalChatVisible, setModalChatVisible] = useState(false)
  const [Alert, setAlert] = useState(false)
  const [AlertPermisoUbicacion, setAlertPermisoUbicacion] = useState(false)
  const [AlertItinierarioInactivo, setAlertItinierarioInactivo] = useState(false)
  const [Incidente, setIncidente] = useState<number>()
  const [IconoVehiculo, setIconoVehiculo] = useState('')
  const [Zoom, setZoom] = useState(14)
  const MapRef = useRef(null as any)
  const [VehiculoSeleccionado, setVehiculoSeleccionado] = useState<Vehiculo | null>()
  const [OportunidadDetalle, setOportunidadDetalle] = useState<HojaRutaApi | null>()
  const [Confirmaciones, setConfirmaciones] = useState(new Map<number, string>())
  const [NumberModal, setNumberModal] = useState(false)
  const CameraPreviewInterval = useRef(null as any)
  const [CameraActive, setCameraActive] = useState(false)

  const apiToaster = useApiToaster()
  const addToast = useToaster()
  const { search } = useLocation()

  if (Capacitor.getPlatform() === 'web') {
    document.location.href = RUTAS_WEB_TRANSPORTISTA.INICIO
    return null
  }

  useEffect(() => {
    let icono = ''
    if (VehiculoSeleccionado) {
      if (VehiculoSeleccionado.tipo_vehiculo.nombre === 'Auto') {
        icono = 'assets/icon/car_orange.png'
      } else if (VehiculoSeleccionado.tipo_vehiculo.nombre === 'Moto') {
        icono = 'assets/icon/moto_orange.png'
      } else {
        icono = 'assets/icon/truck_orange.png'
      }
      setIconoVehiculo(icono)
    }
  }, [VehiculoSeleccionado])

  const handleBackButton = (ev: any) => {
    // @ts-ignore
    ev.detail.register(10, () => {
      if (history.location.pathname === RUTAS_APP.HOME) {
        return false
      } else {
        history.goBack()
      }
    })
  }

  const setup = () => {
    if (Capacitor.getPlatform() === 'android') {
      StatusBar.backgroundColorByHexString('#ff4b00')
    } else if (Capacitor.getPlatform() === 'ios') {
      document.body.style.background = '#ff4b00'
    }

    if (loginStore.transportista) {
      obtenerTransportista(loginStore.transportista.id)
    } else {
      loginStore.reset()
      rootStore.loginStore.setApiToken(null)
      document.location.href = RUTAS.ROOT
    }

    obtenerBalance()
    obtenerTiposVehiculos()
    obtenerRutaActiva()

    rootStore.environment.api.getMensajesPredeterminados().then((result: any) => {
      if (result.kind == 'ok') {
        chatStore.setMensajesPrededeterminados(result.data)
      }
    })

    preVerificarPermisosUbicacion()
    setInterval(preVerificarPermisosUbicacion, 30000)

    setTimeout(() => setReady(true), 700)
  }

  useEffect(() => {
    setup()
  }, [])

  useEffect(() => {
    let currentUrlParams = new URLSearchParams(search)
    setModalChatVisible(currentUrlParams.get('chat') === '1')

    const oportunidad = currentUrlParams.get('oportunidad')
    if (!!oportunidad) {
      obtenerOportunidad(Number(oportunidad))
    } else {
      transportistaStore.setOportunidadDetalle(null)
    }
  }, [search])

  useEffect(() => {
    // se deshabilita el botón de ir atrás
    document.addEventListener('ionBackButton', handleBackButton)
    return () => {
      document.removeEventListener('ionBackButton', handleBackButton)
    }
  }, [])

  useEffect(() => {
    setVehiculoSeleccionado(transportistaStore.vehiculoSeleccionado as any)
  }, [transportistaStore.vehiculoSeleccionado])

  useEffect(() => {
    if (transportistaStore.gpsPermisosBackground) {
      grantPermisosUbicacion().then((gotGPS) => {
        if (gotGPS) {
          iniciarRastreo()
        }
      })
    }
  }, [transportistaStore.gpsPermisosBackground, GotGPSPermission])

  useEffect(() => {
    //console.log('@setItinerarioActivo', transportistaStore.itinerarioActivo)
    setItinerarioActivo(transportistaStore.itinerarioActivo)
  }, [transportistaStore.itinerarioActivo])

  useEffect(() => {
    setHojaRutaActiva(transportistaStore.hojaRutaActiva)
  }, [transportistaStore.hojaRutaActiva])

  useEffect(() => {
    if (!Ready) {
      setFooterReady(Ready)
    } else {
      const timer = setTimeout(() => setFooterReady(Ready), 1000)
      return () => clearTimeout(timer)
    }
  }, [Ready])

  useEffect(() => {
    setOportunidadDetalle(transportistaStore.oportunidadDetalle)
  }, [transportistaStore.oportunidadDetalle])

  useEffect(() => {
    if (ItinerarioActivo) {
      if (!Ubi) {
        if (MapRef.current) {
          setZoom(MapRef.current.getZoom())
        }
        setCenter(string2leaflet_location(ItinerarioActivo.ubicacion))
        setPolylines(null)
      }

      if (!ItinerarioActivo && !OportunidadDetalle && Ubi) {
        if (MapRef.current) {
          setZoom(MapRef.current.getZoom())
        }
        setCenter(Ubi)
        setPolylines(null)
      } else if (ItinerarioActivo && Ubi) {
        const ubi = string2leaflet_location(ItinerarioActivo.ubicacion)
        setCenter(centroUbicaciones(Ubi, ubi))
        if (MapRef.current) {
          setTimeout(() => {
            MapRef.current.fitBounds([Ubi, ubi], { padding: [100, 100] })
          }, 1000)
        }
        setPolylines([Ubi, ubi])
      }
    } else if (OportunidadDetalle && Ubi) {
      setLocationOportunidad(Ubi)
      let convertidos = OportunidadDetalle.itinerarios.map((i: ItinerarioApi) => string2leaflet_location(i.ubicacion))
      convertidos.splice(0, 0, Ubi)
      setPolylines(convertidos)
      setTimeout(() => {
        if (MapRef.current) {
          MapRef.current.fitBounds(convertidos as any, { padding: [100, 100] })
        }
      }, 1000)
    }

    const timeoutId = setTimeout(() => {
      setReady(true)
    }, 300)
    return () => clearTimeout(timeoutId)
  }, [Ubi, ItinerarioActivo, OportunidadDetalle])

  React.useEffect(() => {
    const cameraPreview = new CameraPreview()
    if ((!QRModalVisible && CameraActive) || NumberModal) {
      cameraPreview.stopCamera()
      clearInterval(CameraPreviewInterval.current)
      setCameraActive(false)
    } else if (QRModalVisible) {
      cameraPreview.startCamera({
        camera: 'rear',
        storeToFile: false,
        width: window.innerWidth,
        height: window.innerHeight - 250,
        x: 0,
        y: 115
      }).then(
          (resp) => {
            setCameraActive(true)
          },
          (err) => {
            console.log(err)
          }

      )
      CameraPreviewInterval.current = setInterval(() => {
        cameraPreview.takeSnapshot().then(async (imgData) => {
          try {
            const file = await fetch(`data:image/jpeg;base64,${imgData}`)
            const buffer = await file.arrayBuffer()
            const jpgData = jpeg.decode(buffer, { useTArray: true });
            const qrArray = new Uint8ClampedArray(jpgData.data.buffer);
            const result = jsQR(qrArray, jpgData.width, jpgData.height).data
            if (result && !!HojaRutaActiva && !!HojaRutaActiva.pedido) {
              if (!Confirmaciones.get(HojaRutaActiva.pedido.id || -1)) {
                checkPincode(result)
              }
            }
          } catch(err) {
            console.log('error al leer imagen/qr', err)
          }
        })
      }, 1000)
    }
  }, [QRModalVisible, NumberModal])

  const iniciarRastreo = () => {
    rootStore.environment.locationService.setupRastreoUsuario(
      async (ubicacion: BackgroundGeolocationResponse | null, changed, locprov) => {
        // console.log('background geolocation response', ubicacion, changed)
        if (ubicacion) {
          let ubi = Loc2LatLng(ubicacion)
          transportistaStore.setLocation(ubi)
          setUbi(ubi)
          if (changed) {
            const datos = { usuario: loginStore.id, ubicacion: leaflet_location2string(ubi) }
            rootStore.environment.api.rastrearUsuario(datos).then((result: any) => {
              if (result.kind === 'ok') {
                console.log('Ubicación enviada exitosamente')
              }
            })
          }
        } else {
          const status = await BackgroundGeolocation.checkStatus()
          console.log('[INFO] BackgroundGeolocation authorization status: ' + JSON.stringify(status))
          if (
            (Capacitor.getPlatform() === 'android' && status.authorization !== BackgroundGeolocationAuthorizationStatus.AUTHORIZED) ||
            (Capacitor.getPlatform() === 'ios' && status.authorization === BackgroundGeolocationAuthorizationStatus.NOT_AUTHORIZED)
          ) {
            // we need to set delay or otherwise alert may not be shown
            setTimeout(function () {
              mostrarDialogoGPS('ABi necesita permisos de ubicación, abrir ventana para habilitar el permiso?')
            }, 1000)
          }
        }
      },
    )
  }

  const preVerificarPermisosUbicacion = async () => {
    if (!transportistaStore.gpsPermisosBackground) {
      setAlertPermisoUbicacion(true)
    } else {
      await grantPermisosUbicacion().then((permisos) => {
        if (!permisos) {
          transportistaStore.setGpsPermisosBackground(false)
        }
      })
    }
  }

  const grantPermisosUbicacion = async () => {
    if (Capacitor.getPlatform() === 'ios') {
      setGotGPSPermission(true)
      return true
    }
    if (!(await rootStore.environment.locationService.askPermissionAndOrTurnOnGPS())) {
      transportistaStore.setGpsPermisosBackground(false)
      return false
    } else {
      setGotGPSPermission(true)
    }
    return true
  }

  const [mostrarDialogoGPS, ConfirmDialogGPS] = useConfirm((additionalData: any) => {
    BackgroundGeolocation.showAppSettings()
  })

  const [mostrarDialogo, ConfirmDialog] = useConfirm((additionalData: any) => {
    if ('oportunidades' in additionalData && loginStore.transportista !== null) {
      obtenerOportunidad(additionalData.hoja_ruta_id)
    }
    if ('chat' in additionalData && loginStore.transportista !== null) {
      setModalChatVisible(true)
    }
  })

  const obtenerTransportista = (idTransportista: number) => {
    rootStore.environment.api.adminGetTransportista(idTransportista).then((result) => {
      if (result.kind === 'ok') {
        transportistaStore.setTransportista(result.transportista)
        //
        if (result.transportista.bloqueado) {
          alert('Su perfil de driver se encuentra deshabilitado, por favor contacte con la administración')
          // Cambiar a modo cliente
          cambiarModo(rootStore, history, 10)
        }
      }
    })
  }

  const obtenerOportunidad = (id: number) => {
    rootStore.environment.api.getOportunidad(id).then((result) => {
      if (result.kind === 'ok') {
        transportistaStore.setOportunidadDetalle(result.data)
      } else {
        addToast('La oportunidad ya no se encuentra disponible.')
        transportistaStore.setOportunidadDetalle(null)
      }
    })
  }

  const obtenerTiposVehiculos = () => {
    rootStore.environment.api.tiposVehiculos().then((result) => {
      if (result.kind === 'ok') {
        // @ts-ignore
        rootStore.setTiposVehiculos(result.data.results)
      }
    })
  }

  const obtenerRutaActiva = () => {
    setLoading(true)
    rootStore.environment.api.getHojaRutaActiva().then((result) => {
      if (result.kind === 'ok' && result.data) {
        let hoja = result.data as HojaRutaApi
        transportistaStore.setHojaRutaActiva(hoja)
        if (hoja && hoja.next) {
          transportistaStore.setItinerarioActivo(hoja.next)
          setItinerarioActivo(hoja.next)
        } else {
          transportistaStore.setItinerarioActivo(null)
          setItinerarioActivo(null)
        }
      } else {
        // no tiene rutas activas
        transportistaStore.setHojaRutaActiva(null)
        transportistaStore.setItinerarioActivo(null)
        setItinerarioActivo(null)
        // setPolyline(null)
      }
      setLoading(false)
    })
  }

  const obtenerBalance = () => {
    rootStore.environment.api.getBalanceDetail(20).then((result) => {
      if (result.kind === 'unauthorized') {
        rootStore.loginStore.reset()
        rootStore.loginStore.setApiToken(null)
        document.location.href = RUTAS.ROOT
      } else if (result.kind === 'ok') {
        transportistaStore.setBalance(result.data.pendiente_facturacion)
      }
    })
  }

  const reportarIncidente = () => {
    if (
      transportistaStore.hojaRutaActiva &&
      transportistaStore.hojaRutaActiva.next &&
      transportistaStore.hojaRutaActiva.vehiculo &&
      transportistaStore.lastLoc &&
      Incidente
    ) {
      setLoading(true)
      let ubicacion = Ubi ? leaflet_location2string(Ubi) : ''
      console.log('ubicacion', Ubi)
      rootStore.environment.api
        .reportarIncidente(
          Incidente,
          transportistaStore.hojaRutaActiva.id,
          transportistaStore.hojaRutaActiva.next.id,
          transportistaStore.hojaRutaActiva.vehiculo.id,
          leaflet_location2string(transportistaStore.lastLoc),
        )
        .then((result) => {
          apiToaster(result, 'Incidente reportado exitosamente', 'reportar incidente')
          setLoading(false)
          if (result.kind === 'ok') {
            setModalIncidente(false)
            setModalConfirmarAbandono(false)
            setup()
          }
        })
    }
  }

  const empezarRuta = () => {
    if (!OportunidadDetalle) return
    if (!transportistaStore.vehiculoSeleccionado) {
      addToast('Debe seleccionar un vehículo para iniciar la ruta', 'warning')
      return
    }
    if (!window.confirm('Confirma que desea comenzar la ruta?')) {
      return
    }
    if (!OportunidadDetalle || !VehiculoSeleccionado) {
      return
    }
    history.replace(RUTAS_APP.HOME)
    setLoading(true)
    rootStore.environment.api
      .aceptarRuta(OportunidadDetalle.id)
      .then((result) => {
        setLoading(false)
        apiToaster(result, 'Oportunidad aceptada', 'aceptar la oportunidad')
      })
      .then(() => {
        rootStore.environment.api
          .empezarRuta(OportunidadDetalle, VehiculoSeleccionado)
          .then((result: GenericResponse) => {
            setLoading(false)
            apiToaster(result, 'Ruta iniciada!', 'empezar la ruta')
            if (result.kind === 'ok') {
              // @ts-ignore
              setup()
            }
          })
      })
  }

  const checkPincode = (pincode: string) => {
    if (!ItinerarioActivo) return
    rootStore.environment.api.check_pedido_pin(ItinerarioActivo, pincode).then((result) => {
      if (result.kind === 'ok') {
        let respuesta = result.response
        if (respuesta.success && respuesta.valid) {
          setQRModalVisible(false)
          setNumberModal(false)
          addToast('PIN ingresado correctamente', 'success')
          let c = new Map<number, string>(Confirmaciones)
          respuesta.pedidos.forEach((idpedido: number) => c.set(idpedido, pincode))
          setConfirmaciones(c)
          ejecutarItinerario(c)
        } else {
          addToast('El PIN ingresado no es válido', 'warning')
        }
      } else {
        addToast('Ocurrió un error al verificar el PIN', 'error')
      }
    })
  }

  const ejecutarItinerario = (confirmaciones: any) => {
    console.log('@ejecutarItinerario', confirmaciones)
    console.log('@ItinerarioActivo', ItinerarioActivo)
    if (!ItinerarioActivo) return
    const itinerarioActivo = {...ItinerarioActivo}
    rootStore.environment.api.ejecutarItinerario(itinerarioActivo, confirmaciones).then((result) => {
      if (result.kind === 'ok' && HojaRutaActiva && HojaRutaActiva.pedido) {
        let respuesta = (result as any).response as RespuestaWithInfo
        let respuesta_any = respuesta as any
        if (respuesta.success) {
          if (respuesta_any.msg) {
            addToast(respuesta_any.msg, 'success')
          }
          setConfirmaciones(new Map<number, string>())
          if (respuesta_any.balance) {
            //console.log('@respuesta_any.balance', respuesta_any.balance)
            // Hoja de ruta finalizada
            transportistaStore.setHojaRutaActiva(null)
            transportistaStore.setItinerarioActivo(null)
            setHojaRutaActiva(null)
            setItinerarioActivo(null)
            transportistaStore.setBalance(respuesta_any.balance)
          }
          if (respuesta_any.hoja_ruta) {
            let hoja = respuesta_any.hoja_ruta as HojaRutaApi
            //console.log('@respuesta_any.hoja_ruta', hoja.next)
            transportistaStore.setHojaRutaActiva(hoja)
            setHojaRutaActiva(hoja)
            if (hoja && hoja.next) {
              transportistaStore.setItinerarioActivo(hoja.next)
              setItinerarioActivo(hoja.next)
            } else {
              transportistaStore.setItinerarioActivo(null)
              setItinerarioActivo(null)
            }
          }
          setQRModalVisible(false)
          // Calificar al proveedor
          let itinerarioCalificar: {}
          if (itinerarioActivo.tipo === 20) {
            itinerarioCalificar = {
              tipo_calificacion: itinerarioActivo.tipo,
              id_pedido: HojaRutaActiva.pedido.id,
              nombre: itinerarioActivo.fabricante,
              imagen: itinerarioActivo.fabricante_imagen_perfil,
              id_calificar: itinerarioActivo.fabricante_id,
            }
            rootStore.setPedidoCalificar(itinerarioCalificar)
            setTimeout(() => {
              setCalificacionModalVisible(true)
            }, 1000)
          }
        }
      } else {
        addToast('Ocurrió un error al comprobar el pin, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const llegueClicked = () => {
    if (!ItinerarioActivo) {
      return
    }
    // Reportar al backend que llegó
    rootStore.environment.api.simple_post(`/en-destino/`, {itinerario_id: ItinerarioActivo.id}).then((result) => {
      if (result.kind === 'ok') {
        let respuesta = (result as any).response as RespuestaWithInfo
        if (!respuesta.success) {
          console.log('Error al reportar que se llegó al destino', respuesta.msg)
        }
      } else {
        console.log('Error al reportar que se llegó al destino', result.kind)
      }
    })
    //
    setQRModalVisible(true)
  }

  return (
    <PageApp
      titulo_header={
        !transportistaStore.transportista?.aprobado
          ? `¡Bienvenid@ ${loginStore.transportista && loginStore.transportista.usuario.first_name}!`
          : null
      }
      sub_titulo={
        !transportistaStore.transportista?.aprobado
          ? `id#${loginStore.transportista && loginStore.transportista.id}`
          : null
      }
      boton_derecho={
        transportistaStore.transportista?.aprobado && ItinerarioActivo ? (
          <img style={{ height: 30, width: 30 }} src={'/assets/icon/alert.png'} />
        ) : null
      }
      accion_boton_derecho={() => setModalIncidente(true)}
      mostrar_barra={true}
      subBarraInternaHeader={<>{transportistaStore.transportista?.aprobado && <BarraEstadoTransportista />}</>}
      subBarraExternaHeader={
        <>
          {!!transportistaStore.transportista?.aprobado && transportistaStore.vehiculoSeleccionado && (
            <div
              onClick={() => {
                history.push(RUTAS_APP.VEHICULOS)
              }}
              className={'bar-driver-aprobado vehiculo-seleccionado-cont container-lg'}
            >
              <div>
                <img src={IconoVehiculo} />{' '}
                <span style={{ fontWeight: 'bolder' }}>
                  {transportistaStore.vehiculoSeleccionado?.marca} {transportistaStore.vehiculoSeleccionado?.modelo},{' '}
                  {transportistaStore.vehiculoSeleccionado?.ano},{' '}
                  {transportistaStore.vehiculoSeleccionado?.numero_chapa}{' '}
                </span>
              </div>
            </div>
          )}
        </>
      }
      pageStyles={'background-blanco'}
      // customStyles={{paddingTop: 96 + (transportistaStore.transportista?.aprobado && transportistaStore.vehiculoSeleccionado ? 49 : 0)}}
    >
      {ConfirmDialogGPS}
      {ConfirmDialog}
      {ModalChatVisible &&
        transportistaStore.hojaRutaActiva &&
        transportistaStore.hojaRutaActiva.pedido &&
        ItinerarioActivo && (
          <ChatModal
            pedido={transportistaStore.hojaRutaActiva?.pedido as any}
            setVisible={() => setModalChatVisible(false)}
            visible={ModalChatVisible}
          />
        )}
      {QRModalVisible && HojaRutaActiva && HojaRutaActiva.pedido && ItinerarioActivo && (
        <div>
          <IonModal isOpen={true} cssClass={'modal-hoja-ruta'}>
            <PageApp
              boton_izquierdo={
                <FIcon.X size={30} onClick={() => setQRModalVisible(false)} color={'white'} className={'ml-2'} />
              }
              mostrar_barra={true}
              pageStyles={'background-blanco'}
              sub_titulo={' '} // Fix de tamaño
            >
              <div id="qr-scan-tansportista-container">
                <div className={'qr-reader-div mt-3'}>
                  {/* Aca se muestra el CameraPreview */}
                </div>

                <div className={'footer'}>
                  <div className="row  align-items-center h-100 px-1">
                    <div className="col">
                      <button
                        className={'btn btn-block'}
                        style={{ backgroundColor: 'var(--success)', color: 'white' }}
                        onClick={() => {
                          setNumberModal(true)
                        }}
                        disabled={false}
                      >
                        Introducir el código manualmente
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </PageApp>
          </IonModal>
          <NumpadModal
            titulo={'Ingresar código'}
            maxlen={6}
            visible={NumberModal}
            onAceptar={(numero: string) => {
              checkPincode(numero)
            }}
            color="var(--naranja-transportista)"
            onVolver={() => setNumberModal(false)}

          />
        </div>
      )}
      <IonAlert
        isOpen={AlertItinierarioInactivo}
        onDidDismiss={() => setAlertItinierarioInactivo(false)}
        header={'Reportar incidente'}
        message={'Para reportar un incidente debe estar en un viaje en proceso'}
        buttons={[
          {
            text: 'ok',
            role: 'cancel',
            cssClass: 'cancel-button',
          },
        ]}
      />

      <ModalInfo
        color={'var(--naranja-transportista)'}
        isOpen={ModalIncidente}
        close={() => setModalIncidente(false)}
        titulo={'Abandonar entrega'}
        cssClass="modal-info-incidente"
      >
        <p className="mb-3">¿Pasó algo? Indica el problema</p>
        <div className="listado-tipos-indidentes">
          <div
            onClick={() => {
              setIncidente(TIPOS_INCIDENTE_INDEXES.PROBLEMA_RUTA)
              setModalIncidente(false)
              setModalConfirmarAbandono(true)
            }}
          >
            <FIcon.AlertCircle size={25} /> <strong> Hay un problema con la ruta de destino.</strong>{' '}
          </div>
          <div
            onClick={() => {
              setIncidente(TIPOS_INCIDENTE_INDEXES.DESPERFECTO_MECANICO)
              setModalIncidente(false)
              setModalConfirmarAbandono(true)
            }}
          >
            <FIcon.AlertCircle size={25} /> <strong> Hay un problema con mi rodado.</strong>{' '}
          </div>
          <div
            onClick={() => {
              setIncidente(TIPOS_INCIDENTE_INDEXES.PROBLEMA_PERSONAL)
              setModalIncidente(false)
              setModalConfirmarAbandono(true)
            }}
          >
            <FIcon.AlertCircle size={25} /> <strong> Tengo un problema personal.</strong>{' '}
          </div>
          <div className="mt-3">
            <img src={'assets/icon/atencion-cliente-orange.png'} />{' '}
            <p style={{ color: 'var(--naranja-abi)' }}>Contactar con Staff ABi</p>{' '}
          </div>
        </div>
      </ModalInfo>

      <ModalInfo
        color={'var(--naranja-transportista)'}
        isOpen={ModalConfirmarAbandono}
        close={() => setModalConfirmarAbandono(false)}
        titulo={'Abandonar entrega'}
        cssClass="info-modal-confirm"
      >
        <p className="text-center">¿Estás seguro que deseas abandonar la entrega?</p>
        <div className="d-flex">
          <button
            className="btn-principal-transportista mr-1"
            style={{ backgroundColor: 'var(--danger)' }}
            onClick={() => reportarIncidente()}
          >
            Abandonar
          </button>
          <button
            className="btn-principal-transportista ml-1"
            style={{ backgroundColor: 'var(--success)' }}
            onClick={() => setModalConfirmarAbandono(false)}
          >
            Seguir
          </button>
        </div>
      </ModalInfo>

      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={'Reportar incidente'}
        message={'Confirma que desea reportar el incidente?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'cancel-button',
          },
          {
            text: 'Confirmar',
            cssClass: 'confirm-button-transportista',
            handler: () => reportarIncidente(),
          },
        ]}
      />
      <IonAlert
        isOpen={AlertPermisoUbicacion}
        onDidDismiss={() => setAlertPermisoUbicacion(false)}
        header={'Permisos de ubicación'}
        message={
          'Esta aplicación colecta los datos de ubicación para poder rastraer la ubicación de los drivers, ' +
          'para verificar su disponibilidad y rastrear los pedidos, inclusive cuando no está cerrada o no está en uso'
        }
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'cancel-button',
          },
          {
            text: 'Confirmar',
            cssClass: 'confirm-button-transportista',
            handler: () => {
              transportistaStore.setGpsPermisosBackground(true)
              grantPermisosUbicacion()
            },
          },
        ]}
      />

      {CalificacionModalVisible && <CalificacionTransportista
        color="var(--naranja-transportista)"
        visible={CalificacionModalVisible}
        onCerrar={() => {
          setCalificacionModalVisible(false)
          obtenerRutaActiva()
        }}
        onAceptar={() => {
          setCalificacionModalVisible(false)
          obtenerRutaActiva()
        }}
      />}
      {Loading && (
        <div className="d-flex justify-content-center align-content-center">
          <IonSpinner />
        </div>
      )}
      {!Loading && transportistaStore.transportista && transportistaStore.transportista.aprobado && (
        <div id={'home-mapa-content'} className="page-wrapper">
          {Ready && (
            <MapContainer
              center={Center}
              zoom={Zoom}
              zoomSnap={0.1}
              style={{ height: '100%', width: '100%', flexGrow: 1 }}
              zoomControl={false}
              attributionControl={false}
              whenCreated={(map) => {
                MapRef.current = map
              }}
            >
              <MapController center={Center} />
              <TileLayer
                attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                url="https://{s}.tile.osm.org/{z}/{x}/{y}.png"
              />

              {(ItinerarioActivo || OportunidadDetalle) && Polylines && <Polyline positions={Polylines} />}
              {ItinerarioActivo && itinerario_marker(ItinerarioActivo, itinerario_popup(ItinerarioActivo))}
              {Ubi && <Marker position={Ubi} icon={ICONOS_ITINERARIO.transportista} />}
              {!ItinerarioActivo && LocationOportunidad && (
                <Marker position={LocationOportunidad} icon={ICONOS_ITINERARIO.transportista} />
              )}
              {!ItinerarioActivo &&
                OportunidadDetalle &&
                OportunidadDetalle.itinerarios.map((itinerario) =>
                  itinerario_marker(itinerario, itinerario_popup(itinerario)),
                )}
            </MapContainer>
          )}

          {!Ready && <div className="not-ready-container" />}

          {FooterReady && (
            <div className="footer-container">
              {ItinerarioActivo && (
                <div className={'home-transportista-call'} onClick={() => setModalChatVisible(true)}>
                  <IoChatbubbleEllipsesOutline color="var(--naranja-transportista)" size={45} />
                </div>
              )}
              {!!ItinerarioActivo && (
                <div className={'direccion-destino'}>
                  <a href={'https://maps.google.com/?q=' + ItinerarioActivo.ubicacion + ''}>
                    <img className={'imagen-entregar-pedido'} alt="" src={'assets/icon/ubicacion.png'} />
                    <div>
                      <div
                        style={{
                          maxWidth: '100%',
                          width: '100%',
                          overflow: 'hidden',
                          display: 'flex',
                          justifyContent: 'space-between',
                        }}
                      >
                        <div style={{ flex: 1, overflow: 'hidden', whiteSpace: 'nowrap', textOverflow: 'ellipsis' }}>
                          <strong>{ItinerarioActivo.fabricante ? ItinerarioActivo.fabricante : ItinerarioActivo.usuario}</strong>
                        </div>
                        <div>{!!HojaRutaActiva && <span> Id#{HojaRutaActiva.id}</span>}</div>
                      </div>
                      <p>{ItinerarioActivo.direccion}</p>

                      <div>
                        <span>
                          Tiempo estimado:{' '}
                          <strong>
                            {' '}
                            {Math.ceil(ItinerarioActivo.tiempo_en_transito + ItinerarioActivo.tiempo_parada_calculado)} min
                          </strong>
                        </span>
                      </div>
                    </div>
                  </a>
                </div>
              )}
              {!ItinerarioActivo && !!OportunidadDetalle && (
                <div className="oportunidad-iniciar ">
                  <div>
                    <div style={{}} onClick={() => empezarRuta()}>
                      <IniciarOportunidadIcono />
                    </div>

                    <div className={'oportunidades-list-item'}>
                      <div className="datos-empresa" style={{ lineHeight: '0.8rem' }}>
                        <strong className='wrap' style={{maxWidth: '63vw'}}>
                          {OportunidadDetalle.inicio.fabricante}
                          {OportunidadDetalle.inicio.direccion_itinerario?.nombre && ', '}
                          {OportunidadDetalle.inicio.direccion_itinerario?.nombre}
                        </strong>
                        <p>
                          {OportunidadDetalle.inicio.direccion_itinerario?.calle_principal} N॰{' '}
                          {OportunidadDetalle.inicio.direccion_itinerario?.numero}
                        </p>
                        <div className="datos-punto-itinerario">
                          <img src={'assets/icon/oportunidades.png'} />
                          <strong>Punto de retiro: </strong>
                          <span>
                            {Math.ceil(OportunidadDetalle.inicio.tiempo_en_transito +
                              OportunidadDetalle.inicio.tiempo_parada_calculado)}{' '}
                            min - {OportunidadDetalle.inicio.distancia} km
                          </span>
                          <br />
                          <img src={'assets/icon/arrival_flag.png'} />
                          <strong>Punto de entrega: </strong>
                          <span>
                            {Math.ceil(OportunidadDetalle.fin.tiempo_en_transito + OportunidadDetalle.fin.tiempo_parada_calculado)}{' '}
                            min - {OportunidadDetalle.fin.distancia} km
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              )}
              <HomeTransportistaFooter
                loading={Loading}
                onEjecutarItinerarioClicked={llegueClicked}
                onAbandonarItinerarioCliked={() => setModalIncidente(true)}
              />
            </div>
          )}
        </div>
      )}

      {transportistaStore.transportista && !transportistaStore.transportista.aprobado && !Loading && (
        <CompletarPerfil />
      )}
    </PageApp>
  )
})

export default HomeTransportista
